<template>
    <div class="main-page">
        <div class="h-title">一键发车</div>
        <form class="h-form">
            <div class="car-item shadow" v-for="(item, index) in carList" :key="index" @click="chooseItem(item)">
                <div class="car-item-top" :class="item.checked && 'active'">
                    <div class="car-item-top-l">
                        <div class="line"></div>
                        车牌号：{{ item.FCarNo }}
                    </div>
                    <img src="/static/img/checked.png" v-if="item.checked" />
                    <img src="/static/img/nchecked.png" v-if="!item.checked" />
                </div>
                <div class="car-item-bottom" :class="item.checked && 'active'">
                    <div class="car-item-line">
                        <span>司机：</span>
                        <input v-model="item.DriverName" @click="chooseDriver(item)" />
                    </div>
                    <div class="car-item-line">
                        <span>用车日期：</span>
                        <input v-model="item.FDate" readonly @click.stop="chooseTime('FDate', item)" />
                    </div>
                    <div class="car-item-line">
                        <span>出发地：</span>
                        <input v-model="item.FStartSite" />
                    </div>
                    <div class="car-item-line">
                        <span>目的地：</span>
                        <input style="margin-left: 14px" v-model="item.FEndSite" />
                    </div>
                </div>
            </div>
        </form>
        <van-button type="primary" block size="normal" :color="$config.theme" style="width: calc(100% - 30px); margin: 3px 15px" @click="submit" :loading="loading">提交</van-button>
        <!-- <van-popup v-model="timeShow" position="bottom">
            <van-datetime-picker type="date" title="选择日期" @confirm="confirmTime" />
        </van-popup> -->
        <van-calendar v-model="timeShow" @confirm="confirmTime" color="#409eff" />
    </div>
</template>

<script>
export default {
    name: "CarsUsingAE",
    props: {},
    components: {},
    data() {
        return {
            apis: {
                edit: this._api.VMS_CarUsingRecordEdit,
                create: this._api.VMS_CarUsingRecordCreateBulk,
                list: this._api.VMS_CarUsingRecordManage,
                delete: this._api.VMS_CarUsingRecordDelete,
                audit: this._api.VMS_CarUsingRecordAudit,
                details: this._api.VMS_CarUsingRecordDetails,
            },
            carList: [],
            rules: {},
            timeShow: false,
            loading: false,
            curTimeFieldName: "",
            curRow: {},
        };
    },
    methods: {
        VMS_CarIndex() {
            this._api.VMS_CarIndex().then((res) => {
                if (res) {
                    this.carList = res.data.map((it) => {
                        return {
                            checked: false,
                            FDate: this.$time(new Date()).format("YYYY-MM-DD"),
                            CarId: it.Id,
                            FCarNo: it.FCarNo,
                            DriverName: it.DriverName,
                            DriverId: it.DriverId,
                            FStartSite: "物流中心",
                            FEndSite: "物流中心"
                        };
                    });
                }
            });
        },
        confirmTime(time) {
            this.timeShow = false;
            this.curRow[this.curTimeFieldName] =
                this.$time(time).format("YYYY-MM-DD");
        },
        chooseTime(fieldName, row) {
            this.curRow = row;
            this.curTimeFieldName = fieldName;
            this.timeShow = true;
        },
        async submit() {
            await this.$u.checkForm(this.info, this.rules);
            let submitData = this.carList.filter((it) => it.checked);
            if (submitData.length == 0) return this.$u.msg("请选择车辆");
            this.loading = true;
            let apiFun = this.curId ? this.apis.edit : this.apis.create;
            apiFun(submitData).then((res) => {
                if (res) {
                    this.$u.msg("操作成功！");
                    setTimeout(() => {
                        this.$router.back();
                    }, 200);
                }
                this.loading = false;
            });
        },
        chooseItem(item) {
            item.checked = !item.checked;
        },
        chooseDriver(item) {
            window.temFun = (DriverName) => {
                this.$set(item, "DriverName", DriverName);
            };
            this.$router.push(`/modular/oa/ChooseDriver`);
        },
    },
    mounted() {
        this.VMS_CarIndex();
    },
};
</script>

<style lang="scss" scoped>
.h-title {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    box-shadow: 0 2px 0 0 rgba($color: #000000, $alpha: 0.1);
}

.car-item {
    position: relative;
    width: calc(100% - 30px);
    margin-left: 15px;
    margin-top: 15px;
    font-size: 14px;

    .car-item-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 40px;
        border-bottom: 1px solid #f2f2f2;
        color: #409eff;
        font-weight: 600;
        padding: 0 15px;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        overflow: hidden;

        .car-item-top-l {
            display: flex;
            align-items: center;

            .line {
                width: 3px;
                height: 16px;
                background: #409eff;
                border-radius: 3px;
                margin-right: 8px;
            }
        }

        &.active {
            color: #fff;
            background: #409eff;

            .line {
                background: #fff;
            }
        }
    }

    .car-item-bottom {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        height: 80px;
        padding: 0 15px;

        &.active {
            border: 1px solid #409eff;
            border-top: none;
        }

        .car-item-line {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 50%;
            white-space: nowrap;
            line-height: 40px;
            padding-right: 10px;

            input {
                height: 26px;
                width: 100px;
                text-align: left;
                border: 1px solid #eeeeee;
                padding: 0 3px;
            }
        }
    }
}
</style>